import React, { useState, useEffect } from 'react';
import { Layout, Menu, Typography, Card, Row, Col, Table, Button, Space, Breadcrumb } from 'antd';
import { 
  DashboardOutlined, 
  InboxOutlined, 
  MenuOutlined, 
  UserOutlined,
  LogoutOutlined,
  PlusOutlined,
  EditOutlined,
  CheckOutlined,
  CloseOutlined
} from '@ant-design/icons';
import { MaterialsAPI, MenusAPI, HealthAPI } from './services/api';

const { Header, Content, Sider } = Layout;
const { Title, Text } = Typography;

function App() {
  const [selectedMenuItem, setSelectedMenuItem] = useState('dashboard');
  const [materials, setMaterials] = useState([]);
  const [menus, setMenus] = useState([]);
  const [recentMenus, setRecentMenus] = useState([]);
  const [loading, setLoading] = useState(false);

  // 
  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    setLoading(true);
    try {
      // 
      const materialsData = await MaterialsAPI.getAll();
      setMaterials(materialsData);

      // 
      const menusData = await MenusAPI.getAll();
      setMenus(menusData);
      setRecentMenus(menusData.slice(0, 5)); // 5
    } catch (error) {
      console.error(':', error);
    }
    setLoading(false);
  };

  // 
  const renderDashboard = () => {
    // 
    const pendingMenuColumns = [
      {
        title: 'ID',
        dataIndex: 'id',
        key: 'id',
        width: 80,
      },
      {
        title: '',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: '',
        dataIndex: 'creator',
        key: 'creator',
        render: () => 'A', // 
      },
      {
        title: '',
        dataIndex: 'last_updated',
        key: 'last_updated',
        render: (date) => new Date(date).toLocaleDateString('ja-JP'),
      },
      {
        title: '',
        dataIndex: 'status',
        key: 'status',
        render: () => '', // 
      },
      {
        title: '',
        key: 'actions',
        render: (_, record) => (
          <Space>
            <Button type="primary" size="small" icon={<CheckOutlined />}>
              
            </Button>
            <Button size="small" icon={<CloseOutlined />}>
              
            </Button>
          </Space>
        ),
      },
    ];

    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item></Breadcrumb.Item>
        </Breadcrumb>

        <Row gutter={[16, 16]}>
          {/*  */}
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}></Title>
              <Title level={2} type="primary">{materials.length}</Title>
            </Card>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}></Title>
              <Title level={2} type="success">{menus.length}</Title>
            </Card>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}></Title>
              <Title level={2} type="warning">0</Title>
            </Card>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}></Title>
              <Title level={2}>{recentMenus.length}</Title>
            </Card>
          </Col>
        </Row>

        <Row gutter={[16, 16]} style={{ marginTop: 24 }}>
          {/*  */}
          <Col xs={24} lg={16}>
            <Card 
              title=""
              extra={<Button type="primary" icon={<PlusOutlined />}></Button>}
            >
              <Table
                columns={pendingMenuColumns}
                dataSource={menus.map(menu => ({ ...menu, key: menu.id }))}
                pagination={{ pageSize: 5 }}
                loading={loading}
              />
            </Card>
          </Col>

          {/*  */}
          <Col xs={24} lg={8}>
            <Card title="">
              {recentMenus.map(menu => (
                <div key={menu.id} style={{ marginBottom: 16, padding: 12, backgroundColor: '#f5f5f5', borderRadius: 6 }}>
                  <Text strong>{menu.name}</Text>
                  <br />
                  <Text type="secondary" style={{ fontSize: '12px' }}>
                    : {new Date(menu.last_updated).toLocaleDateString('ja-JP')}
                  </Text>
                  <br />
                  <Button type="link" size="small" icon={<EditOutlined />}></Button>
                </div>
              ))}
            </Card>
          </Col>
        </Row>
      </div>
    );
  };

  // 
  const renderMaterials = () => {
    const materialColumns = [
      {
        title: '',
        dataIndex: 'id',
        key: 'id',
        width: 100,
      },
      {
        title: '',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: '',
        dataIndex: 'unit_price',
        key: 'unit_price',
        render: (price) => `${price}`,
      },
      {
        title: '',
        dataIndex: 'unit',
        key: 'unit',
      },
      {
        title: '',
        dataIndex: 'allergy_info',
        key: 'allergy_info',
        render: (allergy) => allergy || '',
      },
      {
        title: '',
        dataIndex: 'origin_country',
        key: 'origin_country',
      },
      {
        title: '',
        key: 'actions',
        render: (_, record) => (
          <Button type="link" icon={<EditOutlined />}></Button>
        ),
      },
    ];

    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item></Breadcrumb.Item>
        </Breadcrumb>

        <Card 
          title=""
          extra={
            <Space>
              <Button type="primary" icon={<PlusOutlined />}></Button>
              <Button></Button>
            </Space>
          }
        >
          <Table
            columns={materialColumns}
            dataSource={materials.map(material => ({ ...material, key: material.id }))}
            loading={loading}
            pagination={{ 
              showSizeChanger: true,
              showQuickJumper: true,
              showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`
            }}
          />
        </Card>
      </div>
    );
  };

  // 
  const renderMenus = () => {
    const menuColumns = [
      {
        title: '',
        dataIndex: 'id',
        key: 'id',
        width: 120,
        render: (id) => `${id}-A`,
      },
      {
        title: '',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: '',
        dataIndex: 'selling_price',
        key: 'selling_price',
        render: (price) => `${price}`,
      },
      {
        title: '',
        key: 'cost_rate',
        render: () => '28%', // 
      },
      {
        title: '',
        key: 'allergy',
        render: () => ',,', // 
      },
      {
        title: '',
        key: 'actions',
        render: (_, record) => (
          <Button type="link" icon={<EditOutlined />}></Button>
        ),
      },
    ];

    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item></Breadcrumb.Item>
        </Breadcrumb>

        <Card 
          title=""
          extra={
            <Space>
              <Button type="primary" icon={<PlusOutlined />}></Button>
              <Button></Button>
            </Space>
          }
        >
          <Table
            columns={menuColumns}
            dataSource={menus.map(menu => ({ ...menu, key: menu.id }))}
            loading={loading}
            pagination={{ 
              showSizeChanger: true,
              showQuickJumper: true,
              showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`
            }}
          />
        </Card>
      </div>
    );
  };

  // 
  const renderContent = () => {
    switch (selectedMenuItem) {
      case 'dashboard':
        return renderDashboard();
      case 'materials':
        return renderMaterials();
      case 'menus':
        return renderMenus();
      default:
        return renderDashboard();
    }
  };

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Header style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div style={{ color: 'white', fontSize: '18px', fontWeight: 'bold' }}>
          
        </div>
        <Space style={{ color: 'white' }}>
          <UserOutlined />
          <span></span>
          <Button type="text" icon={<LogoutOutlined />} style={{ color: 'white' }}>
            
          </Button>
        </Space>
      </Header>

      <Layout>
        <Sider width={200} style={{ background: '#fff' }}>
          <Menu
            mode="inline"
            selectedKeys={[selectedMenuItem]}
            style={{ height: '100%', borderRight: 0 }}
            items={[
              {
                key: 'dashboard',
                icon: <DashboardOutlined />,
                label: '',
                onClick: () => setSelectedMenuItem('dashboard')
              },
              {
                key: 'materials',
                icon: <InboxOutlined />,
                label: '',
                onClick: () => setSelectedMenuItem('materials')
              },
              {
                key: 'menus',
                icon: <MenuOutlined />,
                label: '',
                onClick: () => setSelectedMenuItem('menus')
              },
              {
                key: 'users',
                icon: <UserOutlined />,
                label: '',
                onClick: () => setSelectedMenuItem('users')
              }
            ]}
          />
        </Sider>

        <Layout style={{ padding: '0 24px 24px' }}>
          <Content
            style={{
              padding: 24,
              margin: 0,
              minHeight: 280,
              background: '#fff',
              borderRadius: 6,
            }}
          >
            {renderContent()}
          </Content>
        </Layout>
      </Layout>
    </Layout>
  );
}

export default App; 